body {
  margin: 0;
  padding: 0;
  background: #f6f8fa;
  color: #111;
}
button {
  padding: 8px 12px;
  font-size: 14px;
}
pre {
  background: #fff;
  padding: 12px;
  border-radius: 6px;
  box-shadow: 0 1px 2px rgba(0,0,0,0.05);
}

/* App root and page container - ensures pages are responsive and have background */
.app-root {
  min-height: 100vh;
  box-sizing: border-box;
  background: linear-gradient(180deg, #f6f8fa 0%, #eef4fb 100%);
  padding: 18px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

.page-container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 16px;
}

/* Navbar styles */
.app-nav {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  align-items: center;
}

.nav-link {
  text-decoration: none;
  color: #0366d6;
  padding: 8px 12px;
  border-radius: 6px;
  transition: background 150ms ease, color 150ms ease;
  font-weight: 500;
}

.nav-link:hover {
  background: rgba(3, 102, 214, 0.08);
}

.nav-link.active {
  background: #0366d6;
  color: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.06);
}

/* Responsive: stack nav on small screens */
@media (max-width: 600px) {
  .app-nav {
    flex-direction: column;
    align-items: flex-start;
  }
  .nav-link {
    width: 100%;
    padding: 10px 12px;
  }
}

/* Home hero and carousel */
.home-hero {
  position: relative;
  display: flex;
  gap: 24px;
  align-items: center;
  justify-content: space-between;
  padding: 32px;
  border-radius: 10px;
  box-shadow: 0 4px 14px rgba(20, 40, 60, 0.06);
  overflow: hidden; /* contain absolutely positioned backgrounds */
  /* make home hero occupy available viewport height (subtract nav + margins). Adjust if your navbar height differs */
  min-height: calc(100vh - 72px);
  height: calc(100vh - 72px);
  background: linear-gradient(180deg, rgba(255,255,255,0.6) 0%, rgba(247,251,255,0.5) 100%);
}

/* Background slideshow container (absolutely positioned layers) */
.hero-bg {
  position: absolute;
  inset: 0; /* top:0; right:0; bottom:0; left:0 */
  z-index: 0;
  pointer-events: none;
  overflow: hidden;
}

.bg-layer {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center right;
  background-repeat: no-repeat;
  opacity: 0;
  transform: scale(1.03);
  transition: opacity 1200ms ease, transform 1200ms ease;
  filter: none;
}

.bg-layer.active {
  opacity: 1;
  transform: scale(1);
}

/* subtle overlay to improve contrast between text and background */
.hero-bg-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(255,255,255,0.55) 0%, rgba(255,255,255,0.35) 60%, rgba(255,255,255,0.6) 100%);
  z-index: 1;
  pointer-events: none;
}

/* ensure content sits above the bg layers */
.hero-left,
.hero-right {
  position: relative;
  z-index: 2;
}

.hero-left {
  flex: 1 1 420px;
}

.hero-left h2 {
  margin: 0 0 8px 0;
  font-size: 28px;
  color: #0b3954;
}

.hero-left p {
  margin: 0;
  color: #33475b;
  line-height: 1.5;
}

.cta {
  display: inline-block;
  background: #0b79d0;
  color: #fff;
  padding: 10px 16px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
}

.hero-right {
  width: 380px;
  max-width: 42%;
  min-width: 260px;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  background: rgba(255,255,255,0.92); /* slightly translucent to let bg hint through */
}

.carousel {
  display: flex;
  transition: transform 600ms ease;
  will-change: transform;
}

.slide {
  min-width: 100%;
  height: 240px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.carousel-dots {
  position: absolute;
  right: 10px;
  top: 10px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: none;
  background: rgba(3, 102, 214, 0.2);
  cursor: pointer;
  padding: 0;
}

.dot.active {
  background: #0366d6;
  box-shadow: 0 1px 2px rgba(0,0,0,0.12);
}

/* Carousel navigation buttons */
.carousel-nav {
  position: absolute;
  left: 8px;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  pointer-events: none; /* allow clicks through empty space */
  z-index: 5;
}

.nav-btn {
  pointer-events: auto;
  background: rgba(255, 255, 255, 0.85);
  border: none;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  cursor: pointer;
  box-shadow: 0 2px 6px rgba(10, 20, 30, 0.12);
}

.nav-btn:hover {
  background: #fff;
}

.nav-btn:focus {
  outline: 2px solid rgba(3,102,214,0.35);
}

/* visually hidden text for screen readers */
.sr-only {
  position: absolute !important;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

