/* Page background utility: apply to page root containers to show a responsive background image with overlay */
.page-with-bg {
  position: relative;
  overflow: hidden;
  border-radius: 10px; /* match page card corners */
  /* fallback gradient if images are missing */
  background: linear-gradient(180deg, rgba(250,251,252,0.9) 0%, rgba(243,247,250,0.85) 100%);
}

/* The actual background layer (keeps markup unchanged) */
.page-with-bg::before {
  content: '';
  position: absolute;
  inset: 0;
  z-index: 0;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  filter: saturate(0.95) contrast(0.98) brightness(0.94);
  transform: scale(1.02);
  transition: transform 450ms ease, opacity 450ms ease;
  opacity: 1;
}

/* subtle overlay to ensure text remains readable on top of images */
.page-with-bg::after {
  content: '';
  position: absolute;
  inset: 0;
  z-index: 1;
  background: linear-gradient(180deg, rgba(255,255,255,0.65) 0%, rgba(255,255,255,0.55) 40%, rgba(255,255,255,0.65) 100%);
  pointer-events: none;
}

/* Ensure page content sits above the decorative layers */
.page-with-bg > * {
  position: relative;
  z-index: 2;
}

/* Per-page modifiers â€” change the URL to use your own images placed under public/backgrounds/ */
/* If you add images, uncomment the matching background-image line or set the --page-bg-... CSS variable. */
.page-with-bg--admin::before{
  /* layered, colorful bubbles for the Admin page background */
  background-image:
    radial-gradient(circle at 20% 25%, rgba(255,165,0,0.18) 0%, rgba(255,165,0,0.10) 28%, transparent 56%),
    radial-gradient(circle at 80% 70%, rgba(0,200,150,0.14) 0%, rgba(0,200,150,0.07) 30%, transparent 58%),
    radial-gradient(circle at 50% 10%, rgba(139,92,246,0.10) 0%, rgba(139,92,246,0.05) 28%, transparent 55%);
  background-size: 260px 260px, 200px 200px, 300px 300px;
  background-position: 20% 25%, 80% 70%, 50% 10%;
  background-repeat: no-repeat, no-repeat, no-repeat;
  /* slow floating animation for subtle motion */
  animation: adminBubbles 20s ease-in-out infinite;
}
.page-with-bg--reports::before{
  /* layered backgrounds: animated translucent bubbles above the main SVG background */
  background-image:
    radial-gradient(circle at 12% 18%, rgba(11,121,208,0.20) 0%, rgba(11,121,208,0.12) 25%, rgba(11,121,208,0.06) 45%, transparent 60%),
    radial-gradient(circle at 82% 22%, rgba(139,92,246,0.14) 0%, rgba(139,92,246,0.08) 30%, transparent 55%),
    radial-gradient(circle at 60% 72%, rgba(3,102,214,0.12) 0%, rgba(3,102,214,0.06) 28%, transparent 55%),
    url('./backgrounds/reports.svg');
  background-size: 220px 220px, 300px 300px, 180px 180px, cover;
  background-position: 12% 18%, 82% 22%, 60% 72%, center center;
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
  /* gentle floating animation for the bubble layers */
  animation: reportsBubbles 14s ease-in-out infinite;
}
.page-with-bg--citizen::before{ /* background-image: url('/backgrounds/citizen.jpg'); */ background-image: none; }
.page-with-bg--appointment::before{ /* background-image: url('/backgrounds/appointment.jpg'); */ background-image: none; }
.page-with-bg--contact::before{ /* background-image: url('/backgrounds/contact.jpg'); */ background-image: none; }
.page-with-bg--about::before  { /* background-image: url('/backgrounds/about.jpg');   */ background-image: none; }

/* If you'd like to enable images later: place files in frontend/public/backgrounds/ (admin.jpg, reports.jpg, citizen.jpg, appointment.jpg, contact.jpg, about.jpg)
   then either uncomment the background-image lines above or add rules like:
   .page-with-bg--admin::before { background-image: url('/backgrounds/admin.jpg'); }
*/

/* Responsive tweaks: reduce parallax/scale on small screens for performance and readability */
@media (max-width: 768px) {
  .page-with-bg::before { transform: scale(1); background-position: center top; }
  .page-with-bg::after { background: linear-gradient(180deg, rgba(255,255,255,0.75) 0%, rgba(255,255,255,0.65) 40%, rgba(255,255,255,0.75) 100%); }
  .page-with-bg { border-radius: 8px; padding: 12px; }
  .home-hero { min-height: calc(100vh - 64px); height: calc(100vh - 64px); padding: 20px; }
}

@media (max-width: 420px) {
  .page-with-bg::before { background-position: center 20%; }
  .home-hero { min-height: calc(100vh - 56px); height: calc(100vh - 56px); padding: 16px; }
}

/* Responsive adjustments */
@media (max-width: 900px) {
  .home-hero {
    flex-direction: column-reverse;
    padding: 20px;
    background-position: center top;
  }
  .hero-right {
    width: 100%;
    max-width: 100%;
    min-width: 0;
    margin-bottom: 8px;
  }
  .slide {
    height: 200px;
  }
  .carousel-dots {
    position: static;
    flex-direction: row;
    justify-content: center;
    gap: 6px;
    margin-top: 8px;
  }
  .reports-container { padding: 16px; margin: 16px; }
  .reports-grid { flex-direction: column; }
  .reports-table { min-width: 100%; }
  .reports-table th, .reports-table td { white-space: normal; padding: 8px; }
}

@media (max-width: 480px) {
  .reports-actions { flex-direction: column; align-items: stretch; }
  .btn-primary { width: 100%; }
}

/* Contact and About small card variants */
.contact-container,
.about-container {
  max-width: 720px;
  margin: 28px auto;
  padding: 20px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 6px 18px rgba(10, 20, 30, 0.06);
}

/* Contact page: underwater video + moving bubbles */
.page-with-bg--contact {
  position: relative;
}
.page-with-bg--contact::before { background-image: none; }

.page-with-bg--contact .bg-video {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
  pointer-events: none;
}
.page-with-bg--contact .bg-fallback { display: none; }

.water-bubbles {
  position: absolute;
  inset: 0;
  z-index: 1;
  pointer-events: none;
  overflow: hidden;
}

.water-bubble {
  position: absolute;
  bottom: -12%;
  border-radius: 50%;
  background: rgba(255,255,255,0.12);
  border: 1px solid rgba(255,255,255,0.18);
  box-shadow: 0 20px 40px rgba(0,0,0,0.15) inset;
  will-change: transform, left, top;
  /* default duration variable so linters/builders can resolve --dur */
  --dur: 12s;
  /* --dur is set inline per bubble; fallback 12s */
  animation: bubbleRise var(--dur, 12s) linear infinite;
}

.water-bubble .bubble-core {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: radial-gradient(circle at 30% 30%, rgba(255,255,255,0.9) 0%, rgba(255,255,255,0.14) 18%, rgba(255,255,255,0.06) 40%, transparent 60%);
  transform-origin: 50% 50%;
  animation: bubbleSway calc(var(--dur, 12s) / 3) ease-in-out infinite alternate, bubblePulse 3s ease-in-out infinite;
}

@keyframes bubbleRise {
  0% { transform: translateY(0) scale(0.9); opacity: 0; }
  8% { opacity: 0.9; }
  60% { opacity: 0.9; }
  100% { transform: translateY(-130vh) scale(1.06); opacity: 0.2; }
}

@keyframes bubbleSway {
  0% { transform: translateX(0) rotate(0deg); }
  50% { transform: translateX(18px) rotate(6deg); }
  100% { transform: translateX(0) rotate(-2deg); }
}

@keyframes bubblePulse {
  0% { transform: scale(0.98); }
  50% { transform: scale(1.02); }
  100% { transform: scale(0.98); }
}

/* Make the contact overlay cooler and darker for underwater look */
.page-with-bg--contact::after {
  content: '';
  position: absolute;
  inset: 0;
  z-index: 2;
  background: linear-gradient(180deg, rgba(0,18,40,0.38) 0%, rgba(0,18,40,0.28) 40%, rgba(0,18,40,0.36) 100%);
  pointer-events: none;
}

/* Ensure contact content sits above video+bubbles */
.page-with-bg--contact > * { position: relative; z-index: 3; }

/* responsive tweaks: hide video on small devices and keep bubbles */
@media (max-width: 640px) {
  .page-with-bg--contact .bg-video { display: none; }
  .page-with-bg--contact .bg-fallback { display: block; position: absolute; inset: 0; width: 100%; height: 100%; object-fit: cover; z-index: 0; }
}

/* About page: reuse underwater video + bubbles styles from Contact page */
.page-with-bg--about {
  position: relative;
}
.page-with-bg--about::before { background-image: none; }

.page-with-bg--about .bg-video {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
  pointer-events: none;
}
.page-with-bg--about .bg-fallback { display: none; }

/* Make the about overlay slightly warmer than contact for visual variety */
.page-with-bg--about::after {
  content: '';
  position: absolute;
  inset: 0;
  z-index: 2;
  background: linear-gradient(180deg, rgba(3,18,30,0.34) 0%, rgba(3,18,30,0.22) 40%, rgba(3,18,30,0.34) 100%);
  pointer-events: none;
}

.page-with-bg--about > * { position: relative; z-index: 3; }

@media (max-width: 640px) {
  .page-with-bg--about .bg-video { display: none; }
  .page-with-bg--about .bg-fallback { display: block; position: absolute; inset: 0; width: 100%; height: 100%; object-fit: cover; z-index: 0; }
}

@keyframes reportsBubbles {
  0% {
    background-position: 12% 18%, 82% 22%, 60% 72%, center center;
    opacity: 1;
  }
  45% {
    background-position: 16% 14%, 86% 18%, 56% 68%, center center;
    opacity: 0.95;
  }
  70% {
    background-position: 10% 22%, 78% 26%, 64% 76%, center center;
    opacity: 1;
  }
  100% {
    background-position: 12% 18%, 82% 22%, 60% 72%, center center;
    opacity: 1;
  }
}

@keyframes adminBubbles {
  0% {
    background-position: 20% 25%, 80% 70%, 50% 10%;
    opacity: 1;
    transform: scale(1);
  }
  40% {
    background-position: 16% 20%, 84% 66%, 54% 14%;
    opacity: 0.95;
    transform: scale(1.01);
  }
  70% {
    background-position: 22% 30%, 76% 74%, 46% 8%;
    opacity: 1;
    transform: scale(0.995);
  }
  100% {
    background-position: 20% 25%, 80% 70%, 50% 10%;
    opacity: 1;
    transform: scale(1);
  }
}
